package io.github.dennistsar.sirs_kobweb.misc

import com.varabyte.kobweb.compose.css.Overflow
import com.varabyte.kobweb.compose.css.WhiteSpace
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.styleModifier

fun Modifier.textEllipsis(): Modifier =
    overflow(Overflow.Hidden)
        .whiteSpace(WhiteSpace.NoWrap)
        .styleModifier { property("text-overflow", "ellipsis") }

fun Modifier.smallCapsFont(): Modifier =
    styleModifier { property("font-variant", "small-caps") }

@Suppress("UNUSED_PARAMETER")
fun jsBalanceTextById(id: String) {
    js("balanceText('#'+id, {watch: true})")
}

@Suppress("UNUSED_PARAMETER")
fun jsFormatNum(
    num: Number,
    minIntDigits: Int = 1,
    maxIntDigits: Int = 0,
    minDecDigits: Int = 0,
    maxDecDigits: Int = 0,
): String {
    return js(
        "(num).toLocaleString('en-US', " +
                "{minimumFractionDigits: minDecDigits, maximumFractionDigits: maxDecDigits, " +
                "minimumIntegerDigits: minIntDigits, maximumIntegerDigits: maxIntDigits}" +
                ")"
    ) as String
}