package io.github.dennistsar.sirs_kobweb.states

import Entry
import androidx.compose.runtime.*
import getRatingAve
import getRatingStats
import getTotalRatings
import io.github.dennistsar.sirs_kobweb.misc.jsFormatNum
import io.github.dennistsar.sirs_kobweb.misc.plusElementAtStart

@Stable
class ProfSummaryVM(
    entriesByProf: Map<String, List<Entry>>,
    selectedProf: String,
    deptCode: String,
    deptUrl: String,
    teachingCourses: List<String>,
    getCourseUrl: (String) -> String,
    goToDeptData: () -> Unit,
    goToCourseData: (String) -> Unit,
) {
    private val profEntries = entriesByProf[selectedProf]
        ?: throw IllegalStateException("Prof not found ($selectedProf)")

    var selectedQ by mutableStateOf(7)

    var selectedCourse by mutableStateOf(0)
    private val courses = profEntries
        .map { it.course }
        .distinct()
        .sorted()
        .plusElementAtStart("All Courses")
    val coursesToDisplay = courses.associateWith { it in teachingCourses }

    val graphNums by derivedStateOf {
        profEntries.run {
            if (selectedCourse != 0) filter { it.course == courses[selectedCourse] }
            else this
        }.getTotalRatings()[selectedQ]
    }
    val graphLabel get() = if (selectedQ < 7) "Disagree -> Agree" else "Poor -> Excellent"

    val average get() = jsFormatNum(graphNums.getRatingAve(), minDecDigits = 2, maxDecDigits = 2)
    val numResponses get() = graphNums.sum()

    private val deptAveComparison by derivedStateOf {
        val deptAves = entriesByProf.map { (_, entries) ->
            entries.getTotalRatings().map { it.getRatingStats() }
        }
        AveComparisonData(
            average = jsFormatNum(deptAves.map { it[selectedQ].first }.average(), minDecDigits = 2, maxDecDigits = 2),
            totalNum = deptAves.size.toString(),
            url = deptUrl,
            urlText = deptCode,
            onLinkClick = { goToDeptData() }
        )
    }

    private val allProfCourseStats = entriesByProf.map { (_, entries) ->
        entries.groupBy { it.course }
            .filterKeys { it in courses }
            .mapValues { (_, entries) -> entries.getTotalRatings() }
    }
    private val courseAveComparison by derivedStateOf {
        val course = courses[selectedCourse].takeIf { selectedCourse != 0 } ?: return@derivedStateOf null
        val courseAve = allProfCourseStats.mapNotNull { ratingsByCourse ->
            ratingsByCourse[course]?.let { it[selectedQ].getRatingStats().first }
        }
        AveComparisonData(
            average = jsFormatNum(courseAve.average(), minDecDigits = 2, maxDecDigits = 2),
            totalNum = courseAve.size.toString(),
            url = getCourseUrl(course),
            urlText = "$deptCode:$course",
            onLinkClick = { goToCourseData(course) }
        )
    }

    val aveComparison get() = courseAveComparison ?: deptAveComparison
}

data class AveComparisonData(
    val average: String,
    val totalNum: String,
    val url: String,
    val urlText: String,
    val onLinkClick: () -> Unit,
)