package io.github.dennistsar.sirs_kobweb.components.sections

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.AlignSelf
import com.varabyte.kobweb.compose.css.TextAlign
import com.varabyte.kobweb.compose.dom.ref
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.attrsModifier
import com.varabyte.kobweb.compose.ui.modifiers.alignSelf
import com.varabyte.kobweb.compose.ui.modifiers.color
import com.varabyte.kobweb.compose.ui.modifiers.rowGap
import com.varabyte.kobweb.compose.ui.modifiers.textAlign
import com.varabyte.kobweb.silk.components.icons.fa.FaGithub
import com.varabyte.kobweb.silk.components.navigation.Link
import com.varabyte.kobweb.silk.components.navigation.LinkStyle
import com.varabyte.kobweb.silk.components.style.*
import com.varabyte.kobweb.silk.components.text.SpanText
import com.varabyte.kobweb.silk.theme.toSilkPalette
import io.github.dennistsar.sirs_kobweb.misc.goatCounterClick
import io.github.dennistsar.sirs_kobweb.misc.jsGoatBindEvents
import org.jetbrains.compose.web.css.cssRem

val FooterStyle = ComponentStyle.base("footer") {
    Modifier
        .alignSelf(AlignSelf.Center)
        .textAlign(TextAlign.Center)
        .rowGap(.1.cssRem)
}

val OppositeLinkVariant = LinkStyle.addVariant("reversed") {
    val linkColors = colorMode.opposite().toSilkPalette().link
    link {
        Modifier.color(linkColors.default)
    }
    visited {
        Modifier.color(linkColors.visited)
    }
}

@Composable
fun Footer(modifier: Modifier = Modifier) {
    Column(
        FooterStyle.toModifier().then(modifier),
        horizontalAlignment = Alignment.CenterHorizontally,
        ref = ref { jsGoatBindEvents() }
    ) {
        Link(
            path = "https://forms.gle/4WyzSbUx5Ym5n2NY9",
            text = "Have feedback?",
            variant = OppositeLinkVariant,
        )
        SpanText("Not affiliated with Rutgers University")
        Row(verticalAlignment = Alignment.CenterVertically) {
            FaGithub()
            SpanText(" This site is ")
            Link(
                "https://github.com/DennisTsar/RU-SIRS",
                "open source",
                Modifier.goatCounterClick("gh-link", title = "GitHub Link", referrer = "footer"),
                OppositeLinkVariant,
            )
        }
        Row {
            SpanText("Made with ")
            Link(
                "https://github.com/varabyte/kobweb",
                "Kobweb",
                Modifier
                    .goatCounterClick("kobweb-link", title = "Kobweb Link", referrer = "footer")
                    .attrsModifier { attr("referrerpolicy", "no-referrer") },
                OppositeLinkVariant,
            )
        }
    }
}